.header {
  background: -webkit-gradient(linear, left top, right top, color-stop(0, #1c3643), color-stop(25%, #273b46), to(#1e5372));
  background: -webkit-linear-gradient(left, #1c3643 0, #273b46 25%, #1e5372);
  background: -o-linear-gradient(left, #1c3643 0, #273b46 25%, #1e5372);
  background: linear-gradient(90deg, #1c3643 0, #273b46 25%, #1e5372);
}

.title {
  color: #fff;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  font-size: 2em;
  font-weight: normal;
  margin: 0;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin: 0 auto;
  max-width: 1000px;
  padding: .5em 1em;
  width: 100%;
}

.navigation {
  display: -ms-flexbox;
  display: flex;
  padding: 1em;
}

.link {
  color: white;
  text-decoration: none;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
  border: 1px solid white;
  border-radius: .25em;
  padding: .25em .5em;
  margin: 0 .5em;
}
.link:first-of-type {
  margin-left: 0;
}
.link:last-of-type {
  margin-right: 0;
}
.link:hover {
  background-color: white;
  color: black;
  -webkit-transform: scale(1.01);
      -ms-transform: scale(1.01);
          transform: scale(1.01);
}



